(function () {
  'use strict';

  const initiateMenu = () => {
    const mainHeader = document.querySelector(`.main-header`);
    const togglers = [].slice.call(
        mainHeader.querySelectorAll(`.main-header__toggler`)
    );
    const navigations = [].slice.call(mainHeader.querySelectorAll(`.navigation`));
    const mainHeaderTop = mainHeader.querySelector(`.main-header__top`);
    const burger = mainHeader.querySelector(`.main-header__burger`);

    const hideNavigations = () => {
      togglers.forEach((toggler) => {
        toggler.classList.remove(`main-header__toggler--active`);
      });

      navigations.forEach((navigation) => {
        navigation.classList.remove(`navigation--active`);
      });
    };

    togglers.forEach((toggler, index) => {
      toggler.addEventListener(`click`, () => {
        if (!toggler.classList.contains(`main-header__toggler--active`)) {
          hideNavigations();

          toggler.classList.add(`main-header__toggler--active`);
          navigations[index].classList.add(`navigation--active`);
        } else {
          hideNavigations();
        }

        burger.classList.remove(`main-header__burger--active`);
        mainHeaderTop.classList.remove(`main-header__top--active`);
      });
    });

    burger.addEventListener(`click`, () => {
      burger.classList.toggle(`main-header__burger--active`);
      mainHeaderTop.classList.toggle(`main-header__top--active`);

      hideNavigations();
    });

    document.addEventListener(`keydown`, (evt) => {
      if (evt.keyCode === 27) {
        burger.classList.remove(`main-header__burger--active`);
        mainHeaderTop.classList.remove(`main-header__top--active`);

        hideNavigations();
      }
    });
  };

  const initiateTopOffers = () => {
    const topOffers = document.querySelector(`.top-offers`);
    const topOffersSlider = topOffers.querySelector(`.top-offers__slider`);
    const buttonPrevious = topOffers.querySelector(`.top-offers__button--prev`);
    const buttonNext = topOffers.querySelector(`.top-offers__button--next`);
    const pagination = topOffers.querySelector(`.swiper-pagination`);

    const topOffersSwiper = new Swiper(topOffersSlider, {
      direction: `horizontal`,
      slidesPerView: 1,
      allowTouchMove: true,
      loop: true,
      navigation: {
        nextEl: buttonNext,
        prevEl: buttonPrevious
      },
      pagination: {
        el: pagination,
        type: `fraction`
      }
    });
  };

  const initiateBrands = () => {
    const brands = document.querySelector(`.brands`);
    const brandsSlider = brands.querySelector(`.brands__slider`);
    const pagination = brands.querySelector(`.swiper-pagination`);

    const brandsSwiper = new Swiper(brandsSlider, {
      direction: `horizontal`,
      slidesPerView: 1,
      allowTouchMove: true,
      loop: true,
      pagination: {
        el: pagination,
        clickable: true
      }
    });
  };

  const initiateReviews = () => {
    const reviews = document.querySelector(`.reviews`);
    const reviewsSlider = reviews.querySelector(`.reviews__slider`);
    const pagination = reviews.querySelector(`.swiper-pagination`);

    const reviewsSwiper = new Swiper(reviewsSlider, {
      direction: `horizontal`,
      slidesPerView: 3,
      allowTouchMove: true,
      loop: true,
      breakpoints: {
        767: {
          slidesPerView: 1
        },
        1199: {
          slidesPerView: 2
        }
      },
      pagination: {
        el: pagination,
        clickable: true
      }
    });
  };

  initiateMenu();
  initiateTopOffers();
  initiateBrands();
  initiateReviews();

}());
